* {
    margin: 0;
    padding: 0;
}

.main-div {
    min-height: 100vh;
    background: rgb(30, 30, 30);
    display: grid;
    grid-template-columns: 5fr 1fr;
    color: white;
    padding: 2%;
    align-items: center;
}

#project {
    color: rgb(222, 200, 0);
    writing-mode: vertical-lr;
    font-size: 64px;
    width: fit-content;
}

.project-grid {
    display: flex;
    flex-direction: column;
    gap: 3rem;
}

.project-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.project-card:nth-child(odd) {
    order: 1;
}


.projects-main-div {
    background-color: black;
    min-height: 100vh;
}

.projects-main-div h1 {
    padding-top: 7%;
    color: white;
}

.project_url {
    color: white;
}

.icon {
    padding: 10px 20px;
}

.link-icons {
    /* margin: 10px; */
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.skill-set{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3%;
}
.vertical-timeline-element-subtitles {
    /* From https://css.glass */
    background: rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    text-align: center;
    padding: 10px;
    margin-bottom: 10px;
    transition: 1s;
}
.vertical-timeline-element-subtitles:hover{
    transform: scale(1.2);
}