@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Condensed&display=swap');
*{
    margin: 0;
    padding: 0;
}
.main-top{
    
    background-color: rgb(30, 30, 30);
}
.whoami_content{
    padding: 20px;
    background-color: rgb(30, 30, 30);
    min-height: 90vh;
    display: grid;
    grid-template-columns: 1fr 2fr;
    color: white;
    align-items: center;
    margin-left: 10%;
}
#welcome_robot{
    animation-name: levitate;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    /* position: absolute; */
    width: 30%;
    object-fit: contain;
    
}
@keyframes levitate{
    0% { transform: translate(0,  0px); }
    50%  { transform: translate(0, 25px); }
    100%   { transform: translate(0, -0px); }
}

#bio{
    color: rgb(222, 200, 0);
    writing-mode:vertical-lr;
    font-size: 64px;
    width: fit-content;
    transform: rotate(180deg);
}
.side-head{
    display: flex;
}
#bio-title {
    font-size: 64px;
    text-align: right;
    margin-right: 1.5em;
    animation-name: blink;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

@keyframes blink {
    0%, 45% { opacity: 1; }
    50%, 95% { opacity: 0; }
    100% { opacity: 1}
}

.side-Head{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 40px;
}
#bio-content{
    font-size: 24px;
}
.skills{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    color: white;
}
#skills_title{
    color: rgb(222, 200, 0);
}
#skills_title img{
    width: 10%;
}
.skill-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
#skills_title{
    margin-top: 10%;
    align-items: right;
    /* height: 100vh; */
    font-size: 124px;
}
.skill-title{
    font-size: 42px;
    margin: 20px;
}
.code-list{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 20px;
}
.skill-div{
    padding: 20px;
    text-align: center;
    margin: 30px;
    /* From https://css.glass */
    background: rgba(255, 255, 255, 0.08);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(8.1px);
    -webkit-backdrop-filter: blur(8.1px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    transition: 1s;
}
.skill-div:hover{
    transform: rotate3d(2, 1, 1, 25deg);
}
.skill-div p{
    background:none;
    font-size: 1.5em;
    font-weight: 600;
}
.skill-div:hover p:nth-child(odd){
    text-shadow: 1px 4px rgb(222, 200, 0);
}