*{
    margin: 0;
    padding: 0;
}
.main{
    background: rgb(0, 0, 0);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#name{
    color: white;
    z-index: 2;
}
nav{
    /* background: rgb(30, 30, 30); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70px;
    z-index: 10;
    position: fixed;
    width: 100%;
}
nav ul{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    gap: 30px;
    margin-right: 30px;
}
.nav-links{
    list-style: none;
}
.nav-links a{
    text-decoration: none;
    font-size: 18px;
    color: white;
    margin: 8px;
}




.introduction{
    height: 100vh;
    background: rgb(30, 30, 30);
    display: grid;
    grid-template-columns: 1fr 5fr;
    color: white;
    padding: 5%;
    align-items: center;
}
#story{
    color: rgb(222, 200, 0);
    writing-mode:vertical-lr;
    font-size: 64px;
    width: fit-content;
}
#content{
    font-size: 18px;
}
#vishnu{
    height: 100%;
    position: absolute;
    opacity: 0.5;
    /* filter: drop-shadow(4px 4px 10px  rgb(195, 195, 195)); */
}
#overlay{
    height: 100vh;
    width: 100%;
    position: absolute;
    background: linear-gradient(to bottom, rgba(0,0,0,0),rgba(0,0,0,1));
    z-index: 1;
}
